import { useRef } from "react";

const imgSrc = 'data:image/png;base64,'


function SignInPage() {
    const inputRef = useRef();
    const doSignIn = (e) => {
        e.preventDefault();
        const loginURL = `${process.env.REACT_APP_API_URL}/login?staySignedIn=${inputRef.current.checked}`;
        fetch(loginURL, {
            credentials: 'include'
        }).then(res => {
            return res.text();
        }).then((url) => {
            window.location = url;
        }).catch((err) => {
            console.log(err);
        });
    }
    return (
        <div >
            <div onClick={doSignIn}>
                <img src={imgSrc} alt='구글로그인아이콘' />
                <label>구글로 로그인</label>
            </div>

            <br />
            <input type="checkbox" ref={inputRef} name="chk_info"></input>
            <label>로그인 유지</label>
        </div>
    );
}
export default SignInPage;